.component-list{
    display : flex;
    flex-direction: column;
    width: 20.75%;
    margin-left: 10px;
}

.images{
    height: 90%;
    overflow: hidden;
    width: 100%;
    background-size: cover;
    background-position: center;
}

.photos{
    transition: transform 3s;
    background-position: center;
    background-size: cover;
    height: 100%;
    width: 100%;
}

.photos:hover{
    transform: scale(1.25);
}

.list{
    display : flex;
    flex-direction: row; 
}

.name{
    font-size: 13px;
    width: 90%;
}

.prices{
    font-size: 20px;
}
